
.menuImage {
  height: 150px;
  width: 150px;
  border-radius: 50%;
}

.menuContainer {
  position: fixed;
  height: 100%;
  width: 220px;
  top: 0px;
  left: 0px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  background-image: url('../images/city_background.jpg');
  background-size: 300% 100%;
  background-color: #6389A3;
  background-blend-mode: multiply;
  color: white;
}

.aboutMeSection {
  -ms-flex-align: start;
      align-items: flex-start;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: end;
      justify-content: flex-end;
  height: 40px;
  width: 100%
}

.titleSection {
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
  height: 220px;
  width: 100%;
}

.menuItem {
  margin-left: 80px;
  margin-top: 5px;
  color: white;
  text-decoration: none;
}

.menuItem:hover {
  color: lightBlue;
}

.selectedItem {
  margin-left: 80px;
  margin-top: 5px;
  color: #28c9b9;
  text-decoration: none;
}

.selectedItem::after {
  color: #28c9b9;
  content: '>';
}

.selectedItem::before {
  position: absolute;
  width: 0;
	height: 0;
	border-top: 5px solid transparent;
	border-right: 15px solid white;
	border-bottom: 5px solid transparent;
  right: 0px;
  content: "";
  margin-top: 2px;
}

